.LoadingToDo-container {
  position: relative;
  width: 70%;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 24px;
  box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.15);
}
.LoadingToDo-text {
  margin: 24px 0 24px 24px;
  width: calc(100% - 120px);
  font-size: 18px;
  line-height: 24px;
  font-weight: 400;
}
.LoadingToDo-completeIcon,
.LoadingToDo-deleteIcon {
  border-radius: 50px;
  height: 48px;
  width: 48px;
}
.LoadingToDo-completeIcon {
  position: absolute;
  left: 12px;
}
.LoadingToDo-deleteIcon {
  position: absolute;
  top: -24px;
  right: 0;
}
.LoadingToDo-container,
.LoadingToDo-completeIcon,
.LoadingToDo-deleteIcon {
  background: linear-gradient(90deg, hsl(0deg 0% 13%), rgb(200, 199, 199));
  background-size: 400% 400%;
}

@keyframes loadingAnimation {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}