.ToDoItem {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 600px;
  background-color: transparent;
  position: relative;
  margin-top: 24px;
  box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.15);
  border-radius: 10px;
  border: 1px solid #5f6368;
  color: #e1e2d7;
}

.ToDoItem-p {
  margin: 24px 0 24px 24px;
  width: calc(100% - 120px);
  font-size: 16px;
  line-height: 24px;
  font-weight: 400;
}
.ToDoItem-p--complete {
  text-decoration: line-through;
}

.Icon {
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 48px;
  width: 48px;
  font-size: 24px;
  font-weight: bold;
}

.Icon-check {
  position: absolute;
  left: 12px;
}
.Icon-check--active {
  color: #4caf50;
}

.Icon-delete {
  position: absolute;
  top: -24px;
  right: 0;
}
.Icon-delete:hover {
  color: red;
}
.Icon-delete img {
  height: 35px;
  width: 35px;
}

@media (max-width: 660px) {
  .ToDoItem {
    width: 80%;
  }
}